import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

export default class InlineQuestion extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: typeof props.defaultExpanded !== 'undefined' ? !props.defaultExpanded : true
    }
  }

  getState = ()=>{
    return this.state.collapsed ? "" : "expanded";
  }

  toggleState = ()=>{
    this.setState({collapsed: !this.state.collapsed})
  }

  render() {
    const { answer, question } = this.props;
    return (
      <div className="InlineQuestion">
        <div onClick={this.toggleState}>
          <div className={"chevron " + this.getState()}>
            <svg viewBox="0 0 52 52" onClick={ this.toggleState }>
              <line x1={1} y1={25} x2={51} y2={25}/>
              <line x1={1} y1={25} x2={51} y2={25}/>
            </svg>
          </div>
          <h6 className={"question " + this.getState()}>{ question }</h6>
        </div>
        { answer && !this.state.collapsed &&
          answer.map((paragraph, index) =>
            <p className="col-12 col-md-10 answer" key={ index } dangerouslySetInnerHTML={{ __html: paragraph }}>
            {/* { paragraph } */}
            </p>
          )
        }
      </div>
    );
  }
}

InlineQuestion.propTypes = {
  defaultExpanded: PropTypes.bool,
  question: PropTypes.string.isRequired,
  answer: PropTypes.arrayOf(PropTypes.string)
}

InlineQuestion.defaultProps = {
  answer: ['Answer not available'],
  defaultExpanded: false
}